@import url(https://fonts.googleapis.com/css2?family=Hind+Madurai&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Hind+Madurai&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Hind+Madurai&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: "Hind Madurai", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: Hind Madurai;
  max-width: 1024px;
  max-height: 700px;
  margin: 30px;
  background-color: #f5f5f6;
}

.ContentContainer {
  padding: 1%;
  height: 700px;
}

.verticalLine {
  width: 3px;
  height: 97%;
  background-color: #c38a9e;
  margin: 3px 10px;
  margin: 3px;
  display: block;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  max-height: 700px;
}

nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
  font-family: "Hind Madurai", sans-serif;
  font-weight: 700;
  overflow-x: hidden;
  /* When content overflows horizontally, they will be hidden */
  max-height: 700px;
}

.navLinks,
.count {
  display: flex;
  justify-content: space-around;
  width: 15%;
  max-width: 350px;

  position: absolute;
  /* height of navbar */
  height: 690px;
  top: 33px;
  opacity: 1;

  flex-direction: column;
  transform: translateX(-100%);
  transition: transform 0.5s ease-in;
  /* Transition of the navbar */
}

.navLinks {
  align-items: flex-end;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  /* The container of the will have a shadow */
}

.count {
  transform: translateX(-155%);
  top: 0;
}

.navLinks li,
.navLinks NavItems {
  display: inline-flex;
  list-style: none;
  text-decoration: none;
  color: rgb(70, 69, 69);
  letter-spacing: 3px;
  font-weight: 400;
  font-size: 20px;
  height: 100%;
  width: 100%;
  opacity: 0;
}

.navLinks li span,
.navLinks NavItems span {
  justify-self: center;
  align-self: center;
  margin-left: auto;
  margin-right: auto;
  vertical-align: middle;
}

.count li,
.count NavCountItems,
.count div {
  position: relative;
  display: inline-block;

  list-style: none;
  text-decoration: none;

  align-self: flex-end;
  text-align: center;

  width: 50px;
  height: 50px;
  line-height: 47.5px;
  color: rgb(70, 69, 69);
  font-size: 20px;

  font-weight: 400;

  background-color: #f7bacf;

  border-radius: 25px;

  opacity: 0;
}

.navLinks a {
  text-decoration: none;
  color: rgb(70, 69, 69);
  letter-spacing: 3px;
  font-weight: 400;
  font-size: 14px;
}

div.navCountStatic div.navCountAni {
  opacity: 1;
  padding-left: 25px;
  text-transform: capitalize;
  width: 250px;
  background: #f7bacf;
  position: absolute;
  left: 0;
  cursor: pointer;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

div.navCountStatic {
  opacity: 1;
  background: #f7bacf;
  position: absolute;
  z-index: -1;
  cursor: pointer;
  top: 0;
  left: 0;
}

.burger {
  cursor: pointer;
  padding: 10px;
  position: relative;
  z-index: 3;
  background-color: #ffeeff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.burger.shadow {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.burger div {
  width: 25px;
  height: 3px;
  background-color: rgb(70, 69, 69);
  margin: 5px;
  transition: all 0.3s ease;
  display: block;
}

nav .navActive {
  transform: translateX(0%);
}

.toggle .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle .line2 {
  opacity: 0;
}

.toggle .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}
* {
  font-family: "Hind Madurai", sans-serif;
}

.answer {
  min-height: 380px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.card {
  margin: 5px 10px;
  min-height: 275px;
  min-width: 265px;
}

.toggleButton {
  height: 100%;
  border-radius: 5%;
  border: none !important;
  color: #ffedff;
}

.optionName {
  font-family: "Hind Madurai";
  text-transform: uppercase;
  font-size: 1.5em;
  padding-top: 5px;
  background-color: #fce4ec;
}

.imgDiv {
  position: relative;
  width: 200px;
  height: 200px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
}

.img {
  border-radius: 8px;
  display: inline-block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}

.choiceGrid {
  overflow-y: hidden;
  overflow-x: hidden;
}

.question {
  text-align: right;
  align-items: flex-end;
  display: flex;
  flex-direction: column;
  padding-left: 30px;
}

.question h1 {
  width: auto;
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 40px;

  text-align: right;
  letter-spacing: 0.1px;

  color: #373f41;
}

.question button {
  margin: 10px;
  margin-top: 5px;
  margin-right: 30px;
}

